import { Observable, OperatorFunction } from 'rxjs';
import { map } from 'rxjs/operators';

import { CallState, ErrorState, LoadingState } from '../models/call-state.model';

/**
 * Helper function to extract error, if there is one.
 *
 * @param callState - call sate
 */
export function getCallStateError(callState: CallState): string | null {
  if ((callState as ErrorState).error !== undefined) {
    return (callState as ErrorState).error;
  }

  return null;
}

/**
 * Checks for initial state
 *
 * @param callStates - call states
 */
export function isInitialState(...callStates: CallState[]): boolean {
  return callStates.some((state) => state === LoadingState.Init);
}

/**
 * Checks for loading state
 *
 * @param callStates - call states
 */
export function isLoadingState(...callStates: CallState[]): boolean {
  return callStates.some((state) => state === LoadingState.Loading);
}

/**
 * Checks for error state
 *
 * @param callStates - call states
 */
export function isErrorState(...callStates: CallState[]): boolean {
  return callStates.some((state) => Object.prototype.hasOwnProperty.call(state, 'error'));
}

/**
 * Checks for loaded state
 *
 * @param callStates - call states
 */
export function isLoadedState(...callStates: CallState[]): boolean {
  return callStates.every((state) => state === LoadingState.Loaded);
}

/**
 * Operator state map operator
 *
 * @param fn - state mapper function
 */
export function stateMap(fn: (...states: CallState[]) => boolean): OperatorFunction<CallState | CallState[], boolean> {
  return (source: Observable<CallState | CallState[]>): Observable<boolean> =>
    source.pipe(map((state) => fn.call(null, ...(Array.isArray(state) ? state : [state]))));
}
