import { LOAN_JOURNEY_COMMUNICATOR, LoanCommunicationService } from '../../../services/communication.service';
import { ChangeDetectionStrategy, Component, Inject, Input, Optional } from '@angular/core';
import { Observable } from 'rxjs';
import { CONDITIONS, PropertyResolver } from '@backbase/foundation-ang/entitlements';
import {
  Loan,
  LoanDefaultViewType,
  PaymentOptions,
  RetailLoanSummaryDataConfig,
} from '@backbase/loans-collection-ui-ang';
import { ACCOUNT_ALIAS_DISPLAYING_LEVEL, AccountAliasDisplayingLevel } from '@backbase/product-summary-common-ang';
import { InputInlineEditState } from '@backbase/ui-ang/input-inline-edit';

import { LoanDetailsBaseComponent } from '../../base/loan-details/loan-details-base-component';
import { accountEditEntitlementsConfig, getRetailLoanSummaryDataConfig } from './loan-summary.config';
import { LoansStoreService } from '../../../store/loans/store.service';
import { PresentationPaymentOptions } from '@backbase/data-ang/loans';
import { RemoteConfigService } from '@backbase/remote-config-ang';
import { RetailAppRemoteConfig } from '../../../models/remote-config.model';

@Component({
  selector: 'bb-loan-summary',
  templateUrl: './loan-summary.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LoanSummaryComponent extends LoanDetailsBaseComponent<RetailLoanSummaryDataConfig> {
  owedAmountFocus: boolean | undefined;

  /**
   * Loan id
   */
  @Input() set loanId(id: string) {
    this.storeService.fetchLoanById(id);
  }
  /**
   * Stream represents edit states for loan alias
   */
  readonly aliasEditState$: Observable<InputInlineEditState> = this.storeService.aliasEditState$;
  /**
   * Flag represents user permissions to edit an alias
   */
  readonly canEditAlias!: Promise<boolean>;

  readonly PaymentOptions = PaymentOptions;

  readonly LoanDefaultViewType = LoanDefaultViewType;

  constructor(
    storeService: LoansStoreService,
    @Inject(CONDITIONS) private readonly conditions: PropertyResolver,
    @Optional()
    @Inject(ACCOUNT_ALIAS_DISPLAYING_LEVEL)
    private readonly accountAliasDisplayingLevel: AccountAliasDisplayingLevel,
    @Optional() @Inject(LOAN_JOURNEY_COMMUNICATOR) public loanCommunication: LoanCommunicationService,
    private remoteConfig: RemoteConfigService<RetailAppRemoteConfig>,
  ) {
    super(storeService);

    this.accountAliasDisplayingLevel = accountAliasDisplayingLevel || AccountAliasDisplayingLevel.USER;
    this.canEditAlias = this.conditions.resolveEntitlements(
      accountEditEntitlementsConfig[this.accountAliasDisplayingLevel],
    );
  }
  /**
   * Configuration for loan summary container
   */
  getDataConfig({ viewType }: Loan): RetailLoanSummaryDataConfig {
    const owedAmountFocus = this.remoteConfig.getValue('owed_amount_focus');

    return getRetailLoanSummaryDataConfig(viewType, owedAmountFocus);
  }

  /**
   * Updates a loan alias
   *
   * @param name
   */
  onUpdateAlias(name: string): void {
    this.storeService.updateAlias(name);
  }

  /**
   * Cancel(discard) current loan alias update
   */
  onCancelAliasUpdate(): void {
    this.storeService.cancelAliasUpdate();
  }

  onDrawAmount(): void {
    if (this.loanCommunication) {
      this.loanCommunication.navigateToLoanAdvance();
    } else {
      throw new Error('[bb-loans]: Communication service is missing');
    }
  }

  onPayDown(): void {
    if (this.loanCommunication) {
      this.loanCommunication.navigateToLoanPayment();
    } else {
      throw new Error('[bb-loans]: Communication service is missing');
    }
  }

  getAllowedPaymentOption(paymentOptionName: PaymentOptions, options: PresentationPaymentOptions | undefined): boolean {
    return (
      !!options?.paymentOptions.find((option) => option.name === paymentOptionName)?.allowed && !!this.loanCommunication
    );
  }
}
