/** Enum represents different possible loading states of ajax call */
export enum LoadingState {
  Init = 'Init',
  Loading = 'Loading',
  Loaded = 'Loaded',
}

/** Interface represents error state of ajax call */
export interface ErrorState {
  error: string;
}

/**
 * Type represents call state, which allows combining the loading and
 * error states into one property.
 */
export type CallState = LoadingState | ErrorState;
