import { NgModule } from '@angular/core';

import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';

import { PaymentScheduleStoreService } from './store.service';
import { paymentScheduleReducer, paymentScheduleStoreKey } from './reducer';
import { PaymentScheduleEffects } from './effects';

@NgModule({
  imports: [
    StoreModule.forFeature(paymentScheduleStoreKey, paymentScheduleReducer),
    EffectsModule.forFeature([PaymentScheduleEffects]),
  ],
  providers: [PaymentScheduleStoreService],
})
export class PaymentScheduleStoreModule {}
