import { Injectable } from '@angular/core';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs';

import { PresentationAmortizationPayment, GetAmortizationPaymentRequestParams } from '@backbase/data-ang/loans';

import {
  selectAmortizedPayment,
  selectAmortizedPaymentCallState,
  selectAmortizedPayments,
  selectAmortizedPaymentsCallState,
  selectAmortizedPaymentsCurrentPage,
  selectAmortizedPaymentsTotalCount,
} from './selectors';
import { CallState } from '../../models/call-state.model';
import { GetAmortizationScheduleParams, PaymentScheduleState } from './store.model';
import { fetchAmortizationPayment, fetchAmortizationSchedule } from './actions';

@Injectable()
export class PaymentScheduleStoreService {
  /**
   * Stream of api call state for fetching scheduled amortized payments
   */
  readonly amortizedPaymentsCallState$: Observable<CallState> = this.store.select(selectAmortizedPaymentsCallState);
  /**
   * Stream of scheduled amortized payments
   */
  readonly amortizedPayments$: Observable<PresentationAmortizationPayment[]> =
    this.store.select(selectAmortizedPayments);

  /**
   * Stream of api call state for fetching scheduled amortized payment
   */
  readonly amortizedPaymentCallState$: Observable<CallState> = this.store.select(selectAmortizedPaymentCallState);
  /**
   * Stream of scheduled amortized payment
   */
  readonly amortizedPayment$: Observable<PresentationAmortizationPayment | undefined> =
    this.store.select(selectAmortizedPayment);

  /**
   * Stream represents total pages count for scheduled amortized payments
   */
  readonly amortizedPaymentsTotalCount$: Observable<number> = this.store.select(selectAmortizedPaymentsTotalCount);
  /**
   * Stream represents current loaded page for payment schedule table/list
   */
  readonly amortizedPaymentsCurrentPage$: Observable<number> = this.store.select(selectAmortizedPaymentsCurrentPage);

  constructor(private readonly store: Store<PaymentScheduleState>) {}

  /**
   * Fetch amortized payment schedule
   */
  fetchAmortizationSchedule(params: GetAmortizationScheduleParams): void {
    this.store.dispatch(fetchAmortizationSchedule({ params }));
  }

  /**
   * Fetch amortized payment
   */
  fetchAmortizationPayment(params: GetAmortizationPaymentRequestParams): void {
    this.store.dispatch(fetchAmortizationPayment({ params }));
  }
}
