import { Injectable } from '@angular/core';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { Loan } from '@backbase/loans-collection-ui-ang';
import { InputInlineEditState } from '@backbase/ui-ang/input-inline-edit';

import {
  selectAliasUpdateCallState,
  selectLoan,
  selectLoanCallState,
  selectLoans,
  selectLoansCallState,
  selectLoansCurrentPage,
  selectLoansTotalCount,
  selectShowNavigateToOverview,
} from './selectors';
import { fetchAmountOfLoans, fetchLoanById, loadLoans, updateLoanAlias, updateLoanAliasCancel } from './actions';
import { isLoadingState } from '../../utils/call-state.util';
import { CallState } from '../../models/call-state.model';
import { LoansStoreState } from './store.model';

@Injectable()
export class LoansStoreService {
  /**
   * Stream of api call state for fetching loans
   */
  readonly loansCallState$: Observable<CallState> = this.store.select(selectLoansCallState);
  /**
   * Stream of loans
   */
  readonly loans$: Observable<Loan[]> = this.store.select(selectLoans);
  /**
   * Stream of api call state for fetching loan item
   */
  readonly loanCallState$: Observable<CallState> = this.store.select(selectLoanCallState);
  /**
   * Stream of loan item
   */
  readonly loan$: Observable<Loan | undefined> = this.store.select(selectLoan);
  /**
   * Stream indicates whether to show/hide navigate to overview button
   */
  readonly showNavigateToOverview$: Observable<boolean> = this.store.select(selectShowNavigateToOverview);
  /**
   * Stream represents current loaded page for loan list
   */
  readonly loansCurrentPage$: Observable<number> = this.store.select(selectLoansCurrentPage);
  /**
   * Stream represents total pages count for loan list
   */
  readonly loansTotalCount$: Observable<number | undefined> = this.store.select(selectLoansTotalCount);
  /**
   * Stream of alias edit states (mapped from call state)
   */
  readonly aliasEditState$: Observable<InputInlineEditState> = this.store
    .select(selectAliasUpdateCallState)
    .pipe(map((callState) => (isLoadingState(callState) ? InputInlineEditState.LOADING : InputInlineEditState.IDLE)));

  constructor(private readonly store: Store<LoansStoreState>) {}

  /**
   * Fetch loans
   */
  fetchLoans(page: number, append = false): void {
    this.store.dispatch(loadLoans({ page, append }));
  }

  /**
   * Fetch loan by id
   */
  fetchLoanById(id: string): void {
    this.store.dispatch(fetchLoanById({ id }));
  }

  /**
   * Fetch amount of loans
   */
  fetchAmountOfLoans(): void {
    this.store.dispatch(fetchAmountOfLoans());
  }

  /**
   * Update alias
   *
   * @param alias
   */
  updateAlias(alias: string): void {
    this.store.dispatch(updateLoanAlias({ alias }));
  }

  /**
   * Cancel alias update
   */
  cancelAliasUpdate(): void {
    this.store.dispatch(updateLoanAliasCancel());
  }
}
