import { Injectable } from '@angular/core';

import { PresentationLoan } from '@backbase/data-ang/loans';
import { Loan } from '@backbase/loans-collection-ui-ang';

import { LoansJourneyConfigurationService } from '../loans-journey-configuration';

@Injectable()
export class LoanMapperService {
  constructor(private readonly configuration: LoansJourneyConfigurationService) {}

  /**
   * Loan mapper func
   */
  readonly toLoanModel = this._toLoanModel.bind(this);

  private _toLoanModel(loan: PresentationLoan): Loan {
    const loanType = loan.loanType;
    const config = this.configuration.loanTypeMapping[loanType];

    if (!config) {
      throw new Error(`[bb-loans]: "${loanType}" type was not configured for loans journey`);
    }

    return {
      ...loan,
      viewType: config.viewType,
      loanTypeAlias: config.typeAlias || loanType,
    };
  }
}
