import { HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';

import {
  GetAmortizationScheduleRequestParams,
  PresentationAmortizationSchedule,
  PresentationLoans,
  GetAmortizationPaymentRequestParams,
  PresentationAmortizationPayment,
  LoansHttpService,
} from '@backbase/data-ang/loans';
import { Loan } from '@backbase/loans-collection-ui-ang';

import { LoansJourneyConfigurationService } from '../loans-journey-configuration';
import { LoansResponse, PaymentScheduleResponse } from '../models/loans-response.model';
import { LoanMapperService } from './loan-mapper.service';

@Injectable()
export class LoansService {
  constructor(
    private readonly loansHttpService: LoansHttpService,
    private readonly loanMapper: LoanMapperService,
    private readonly configurationService: LoansJourneyConfigurationService,
  ) {}

  getLoans(from: number): Observable<LoansResponse> {
    const size = this.configurationService.loanList.itemsPerPage;

    return this.loansHttpService.getLoans({ from, size }, 'response').pipe(
      map((response) => ({
        items: (response.body as PresentationLoans).loans.map(this.loanMapper.toLoanModel),
        totalCount: this.getTotalCount(response),
      })),
    );
  }

  getLoanById(loanId: string): Observable<Loan> {
    return this.loansHttpService.getLoanById({ loanId }).pipe(map(this.loanMapper.toLoanModel));
  }

  getLoansAmount(): Observable<number> {
    return this.loansHttpService.headLoans('response').pipe(map(this.getTotalCount));
  }

  getAmortizationSchedule(params: GetAmortizationScheduleRequestParams): Observable<PaymentScheduleResponse> {
    return this.loansHttpService.getAmortizationSchedule(params, 'response').pipe(
      map((response) => ({
        items: (response.body as PresentationAmortizationSchedule).payments,
        totalCount: this.getTotalCount(response),
      })),
    );
  }

  getAmortizationPayment(params: GetAmortizationPaymentRequestParams): Observable<PresentationAmortizationPayment> {
    return this.loansHttpService.getAmortizationPayment(params);
  }

  private getTotalCount({ headers }: HttpResponse<unknown>): number {
    const totalCount = headers.get('x-total-count');

    if (!totalCount) {
      throw new Error('[bb-loans]: Invalid `x-total-count` header param');
    }

    return parseInt(totalCount, 10);
  }
}
