import { Action, createReducer, on } from '@ngrx/store';

import {
  fetchAmountOfLoansError,
  fetchAmountOfLoansSuccess,
  fetchLoanById,
  fetchLoanByIdError,
  fetchLoanByIdSuccess,
  loadLoans,
  loadLoansError,
  loadLoansSuccess,
  updateLoanAlias,
  updateLoanAliasError,
  updateLoanAliasSuccess,
} from './actions';
import { LoansStoreState } from './store.model';
import { LoadingState } from '../../models/call-state.model';

export const storeKey = 'loansJourneyBase';

export const initialState: LoansStoreState = {
  loans: [],
  loansCurrentPage: 0,
  loansTotalCount: undefined,
  loansCallState: LoadingState.Init,
  loan: undefined,
  loanCallState: LoadingState.Init,
  aliasUpdateCallState: LoadingState.Init,
};

const _loansReducer = createReducer<LoansStoreState>(
  initialState,
  on(loadLoans, (state, { page }) => ({ ...state, loansCallState: LoadingState.Loading, loansCurrentPage: page })),
  on(loadLoansSuccess, (state, { append, response: { items: loans, totalCount } }) => ({
    ...state,
    loans: append ? [...state.loans, ...loans] : loans,
    loansTotalCount: totalCount,
    loansCallState: LoadingState.Loaded,
  })),
  on(loadLoansError, (state, { error }) => ({
    ...state,
    loansCallState: { error },
  })),
  on(fetchLoanById, (state) => ({ ...state, loanCallState: LoadingState.Loading })),
  on(fetchLoanByIdSuccess, (state, { loan }) => ({
    ...state,
    loan,
    loanCallState: LoadingState.Loaded,
  })),
  on(fetchLoanByIdError, (state, { error }) => ({
    ...state,
    loanCallState: { error },
  })),
  on(fetchAmountOfLoansSuccess, (state, { totalCount }) => ({
    ...state,
    loansTotalCount: totalCount,
  })),
  on(fetchAmountOfLoansError, (state) => ({
    ...state,
    loansTotalCount: undefined,
  })),
  on(updateLoanAlias, (state) => ({
    ...state,
    aliasUpdateCallState: LoadingState.Loading,
  })),
  on(updateLoanAliasSuccess, (state) => ({
    ...state,
    aliasUpdateCallState: LoadingState.Loaded,
  })),
  on(updateLoanAliasError, (state, { error }) => ({
    ...state,
    aliasUpdateCallState: { error },
  })),
);

export function loansReducer(state: LoansStoreState, action: Action) {
  return _loansReducer(state, action);
}
