import { Action, createReducer, on } from '@ngrx/store';

import {
  fetchAmortizationPayment,
  fetchAmortizationPaymentError,
  fetchAmortizationPaymentSuccess,
  fetchAmortizationSchedule,
  fetchAmortizationScheduleError,
  fetchAmortizationScheduleSuccess,
} from './actions';
import { LoadingState } from '../../models/call-state.model';
import { PaymentScheduleState } from './store.model';

export const paymentScheduleStoreKey = 'loansJourneyPaymentSchedule';

export const initialState: PaymentScheduleState = {
  amortizedPayments: [],
  amortizedPaymentsTotalCount: 0,
  amortizedPaymentsCurrenPage: 0,
  amortizedPaymentsCallState: LoadingState.Init,
  amortizedPayment: undefined,
  amortizedPaymentCallState: LoadingState.Init,
};

const reducer = createReducer<PaymentScheduleState>(
  initialState,
  on(fetchAmortizationSchedule, (state, { params: { from } }) => ({
    ...state,
    amortizedPaymentsCallState: LoadingState.Loading,
    amortizedPaymentsCurrenPage: from as number,
  })),
  on(fetchAmortizationScheduleSuccess, (state, { append, response: { items, totalCount } }) => ({
    ...state,
    amortizedPayments: append ? [...state.amortizedPayments, ...items] : items,
    amortizedPaymentsTotalCount: totalCount,
    amortizedPaymentsCallState: LoadingState.Loaded,
  })),
  on(fetchAmortizationScheduleError, (state, { error }) => ({
    ...state,
    amortizedPaymentsCallState: { error },
  })),
  on(fetchAmortizationPayment, (state) => ({
    ...state,
    amortizedPaymentCallState: LoadingState.Loading,
  })),
  on(fetchAmortizationPaymentSuccess, (state, { amortizedPayment }) => ({
    ...state,
    amortizedPayment,
    amortizedPaymentCallState: LoadingState.Loaded,
  })),
  on(fetchAmortizationPaymentError, (state, { error }) => ({
    ...state,
    amortizedPaymentCallState: { error },
  })),
);

export function paymentScheduleReducer(state: PaymentScheduleState, action: Action) {
  return reducer(state, action);
}
