import { ChangeDetectionStrategy, Component, Inject } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DOCUMENT } from '@angular/common';
import { filter, pluck, takeUntil } from 'rxjs/operators';
import { combineLatest, Observable } from 'rxjs';

import { NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';

import { DestroyableComponent, Loan } from '@backbase/loans-collection-ui-ang';
import { PresentationAmortizationPayment } from '@backbase/data-ang/loans';

import { scheduleIdParam } from '../../../../../config/route-params.model';
import { PaymentScheduleStoreService } from '../../../../../store/payment-schedule/store.service';
import { isErrorState, isLoadingState, stateMap } from '../../../../../utils/call-state.util';
import { LoansStoreService } from '../../../../../store/loans/store.service';
import { RemoteConfigService } from '@backbase/remote-config-ang';
import { RetailAppRemoteConfig } from '../../../../../models/remote-config.model';

/**
 * @dynamic
 */
@Component({
  selector: 'bb-payment-schedule-details-wrapper',
  templateUrl: './payment-schedule-details-wrapper.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class PaymentScheduleDetailsWrapperComponent extends DestroyableComponent {
  private readonly callStates$ = combineLatest([
    this.paymentScheduleStoreService.amortizedPaymentCallState$,
    this.loansStoreService.loanCallState$,
  ]);
  /**
   *  Stream of payment id param
   */
  readonly paymentId$: Observable<string> = this.activatedRoute.params.pipe(pluck(scheduleIdParam));
  /**
   * Stream of scheduled amortized payment
   */
  readonly amortizedPayment$: Observable<PresentationAmortizationPayment | undefined> =
    this.paymentScheduleStoreService.amortizedPayment$;
  /**
   * Flag represents loading state
   */
  readonly isLoading$: Observable<boolean> = this.callStates$.pipe(stateMap(isLoadingState));
  /**
   * Flag represents error state
   */
  readonly isErrorState$: Observable<boolean> = this.callStates$.pipe(stateMap(isErrorState));
  /**
   * Stream of loan
   */
  readonly loan$: Observable<Loan | undefined> = this.loansStoreService.loan$;
  /**
   * Hide or show payment status
   */
  readonly showPaymentStatus = this.remoteConfig.getValue('show_payment_status_retail_loans');
  /**
   * Modal options
   */
  readonly modalOptions: NgbModalOptions = {
    backdrop: 'static',
    keyboard: true,
    centered: true,
    ariaDescribedBy: 'bb-payment-schedule-details-modal-body',
  };

  constructor(
    private readonly activatedRoute: ActivatedRoute,
    private readonly router: Router,
    private readonly paymentScheduleStoreService: PaymentScheduleStoreService,
    private readonly loansStoreService: LoansStoreService,
    @Inject(DOCUMENT) private readonly document: Document,
    private readonly remoteConfig: RemoteConfigService<RetailAppRemoteConfig>,
  ) {
    super();

    combineLatest([this.paymentId$, this.loan$])
      .pipe(
        filter(([, loan]) => Boolean(loan)),
        takeUntil(this.componentDestroyed$),
      )
      .subscribe(([amortizationPaymentId, loan]) => {
        this.paymentScheduleStoreService.fetchAmortizationPayment({ loanId: (loan as Loan).id, amortizationPaymentId });
      });
  }

  onCloseModal(): void {
    this.router.navigate(['..'], { relativeTo: this.activatedRoute });
  }

  onPrint(): void {
    this.document.defaultView?.print();
  }
}
