import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';

import { LoansTab } from '@backbase/loans-collection-ui-ang';

import { LoansStoreService } from '../../../store/loans/store.service';
import { DataTypedRoute } from '../../../models/tab-route.model';

@Component({
  selector: 'bb-loan-details-wrapper',
  templateUrl: './loan-details-wrapper.component.html',
})
export class LoanDetailsWrapperComponent {
  /**
   * Stream of account ids from router params
   */
  readonly accountId$: Observable<string> = this.activatedRoute.params.pipe(
    map(({ selectedAccount }) => selectedAccount),
  );

  /**
   * Whether to show / hide navigate to overview btn
   */
  readonly showNavigationButton$: Observable<boolean> = this.storeService.showNavigateToOverview$;

  /**
   * Array of tabs to be displayed
   */
  readonly tabs = this.createRouteTabs((this.activatedRoute.snapshot?.routeConfig?.children as DataTypedRoute[]) || []);

  constructor(
    private readonly router: Router,
    private readonly activatedRoute: ActivatedRoute,
    private readonly storeService: LoansStoreService,
  ) {
    this.storeService.fetchAmountOfLoans();
  }

  /**
   * Navigates to the list overview view
   *
   * TODO: set up relative routing
   */
  navigateToAccounts(): void {
    this.router.navigate(['my-accounts/list']);
  }

  private createRouteTabs(routes: DataTypedRoute[]): LoansTab[] {
    return routes
      .filter(({ path, data }) => path && data?.titleFn)
      .map(({ path, data }) => ({
        title: data.titleFn(),
        route: path,
      }));
  }
}
