import { ChangeDetectionStrategy, Component } from '@angular/core';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';
import { combineLatest, Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { PresentationAmortizationPayment } from '@backbase/data-ang/loans';

import { LoadMoreParams } from '@backbase/ui-ang/load-button';
import { MediaQueryService } from '@backbase/ui-ang/media-query-lib';

import { loanIdParamKey } from '../../../../config/route-params.model';
import { isErrorState, isLoadingState, stateMap } from '../../../../utils/call-state.util';
import { LoansJourneyConfigurationService } from '../../../../loans-journey-configuration';
import { PaymentScheduleStoreService } from '../../../../store/payment-schedule/store.service';
import { GetAmortizationScheduleParams } from '../../../../store/payment-schedule/store.model';
import { LoansJourneyLoansPaymentScheduleConfig } from '../../../../models/journey-configuration.model';
import { RemoteConfigService } from '@backbase/remote-config-ang';
import { RetailAppRemoteConfig } from '../../../../models/remote-config.model';

// TODO: move to separate and reorganize the flow
@Component({
  selector: 'bb-payment-schedule-wrapper',
  templateUrl: './payment-schedule-wrapper.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class PaymentScheduleWrapperComponent {
  /**
   * Stream of Amortized Payments
   */
  readonly amortizedPayments$: Observable<PresentationAmortizationPayment[]> = this.storeService.amortizedPayments$;
  /**
   * Payment schedule configuration
   */
  readonly scheduleConfig: LoansJourneyLoansPaymentScheduleConfig = this.configurationService.paymentSchedule;

  /**
   * Hide or show payment status in the table
   */
  readonly showPaymentStatus = this.remoteConfig.getValue('show_payment_status_retail_loans');

  /**
   * Flag represents error state
   */
  readonly isErrorState$: Observable<boolean> = this.storeService.amortizedPaymentsCallState$.pipe(
    stateMap(isErrorState),
  );

  /**
   * Stream represents total amount of the pages
   */
  readonly amortizedPaymentsTotalCount$: Observable<number> = this.storeService.amortizedPaymentsTotalCount$;

  /**
   * Stream represent current loaded page number
   */
  readonly amortizedPaymentsCurrentPage$: Observable<number> = this.storeService.amortizedPaymentsCurrentPage$;

  /**
   * Flag represents the tablet screen resolution
   */
  readonly isTabletResolution$: Observable<boolean> = this.mediaQueryService.isMediaBreakpointMatches(
    'max-width',
    'lg',
  );

  private readonly _isLoading$ = combineLatest([
    this.isTabletResolution$,
    this.storeService.amortizedPaymentsCallState$,
    this.amortizedPaymentsCurrentPage$,
  ]);
  /**
   * Flag represents page loading state
   */
  readonly isPageLoading$: Observable<boolean> = this._isLoading$.pipe(
    map(
      ([isTabletResolution, callState, currentPage]) =>
        isLoadingState(callState) && !(isTabletResolution && currentPage > 0),
    ),
  );
  /**
   * Flag represents load more button loading state
   */
  readonly isButtonLoading$: Observable<boolean> = this._isLoading$.pipe(
    map(
      ([isTabletResolution, callState, currentPage]) =>
        isLoadingState(callState) && isTabletResolution && currentPage > 0,
    ),
  );
  /**
   * Flag represents visibility of load more button
   */
  readonly isLoadMoreVisible$: Observable<boolean> = combineLatest([
    this.amortizedPaymentsCurrentPage$,
    this.amortizedPaymentsTotalCount$,
    this.amortizedPayments$,
  ]).pipe(map(([page, total, { length }]) => !(page === 0 && length === total)));

  /**
   * Flag represents empty state
   */
  readonly isEmptyState$: Observable<boolean> = this.storeService.amortizedPaymentsTotalCount$.pipe(
    map((totalCount) => totalCount === 0),
  );

  private readonly baseReqParams: GetAmortizationScheduleParams = {
    loanId: (this.activatedRoute.snapshot.parent as ActivatedRouteSnapshot).params[loanIdParamKey],
    size: this.scheduleConfig.itemsPerPage,
    from: 0,
    append: false,
  };

  constructor(
    private readonly configurationService: LoansJourneyConfigurationService,
    private readonly storeService: PaymentScheduleStoreService,
    private readonly mediaQueryService: MediaQueryService,
    private readonly activatedRoute: ActivatedRoute,
    private readonly router: Router,
    private readonly remoteConfig: RemoteConfigService<RetailAppRemoteConfig>,
  ) {
    this.storeService.fetchAmortizationSchedule(this.baseReqParams);
  }

  /**
   * Load loans on pagination page changes
   *
   * @param page
   */
  onPageChange(page: number): void {
    this.storeService.fetchAmortizationSchedule({ ...this.baseReqParams, from: page });
  }

  /**
   * Load loans on load more button click
   *
   * @param from
   */
  onLoadMore({ from }: LoadMoreParams): void {
    this.storeService.fetchAmortizationSchedule({ ...this.baseReqParams, from, append: true });
  }

  /**
   * Emits in item select
   */
  onItemSelect({ id }: PresentationAmortizationPayment): void {
    this.router.navigate([id], { relativeTo: this.activatedRoute });
  }
}
