import { provideRoutes, RouterModule, Routes } from '@angular/router';
import { CommonModule } from '@angular/common';
import { ModuleWithProviders, NgModule } from '@angular/core';

import { EntitlementsModule } from '@backbase/foundation-ang/entitlements';
import {
  LoanInfoItemModule,
  LoanInfoPrintButtonModule,
  LoanItemNameModule,
  LoansCollectionUiAngModule,
  LoansHeadingModule,
} from '@backbase/loans-collection-ui-ang';
import { AccountStatementBusinessWidgetModule } from '@backbase/account-statement-business-widget-ang';

import { LoansJourneyConfigurationService } from './loans-journey-configuration';
import { loansRoutes } from './loans-journey.routes';
import { LoansStoreModule } from './store/loans/store.module';
import { PaymentScheduleStoreModule } from './store/payment-schedule/store.module';
import { LoansService } from './services/loans.service';
import { LoanMapperService } from './services/loan-mapper.service';
import { ArrangementsService } from './services/arrangements.service';
import { LoanSummaryComponent } from './components/loan-details/loan-summary/loan-summary.component';
import { LoanInfoWrapperComponent } from './components/loan-details/tabs/loan-info-wrapper/loan-info-wrapper.component';
import { LoanDetailsWrapperComponent } from './components/loan-details/loan-details-wrapper/loan-details-wrapper.component';
import { LoanTransactionsWrapperComponent } from './components/loan-details/tabs/loan-transactions-wrapper/loan-transactions-wrapper.component';
import { PaymentScheduleWrapperComponent } from './components/loan-details/tabs/payment-schedule-wrapper/payment-schedule-wrapper.component';
import { PaymentScheduleDetailsWrapperComponent } from './components/loan-details/tabs/payment-schedule-wrapper/payment-schedule-details-wrapper/payment-schedule-details-wrapper.component';
import { PaymentScheduleDetailsModalHeaderComponent } from './components/loan-details/tabs/payment-schedule-wrapper/payment-schedule-details-wrapper/payment-schedule-details-modal-header/payment-schedule-details-modal-header.component';
import { LoanInfoRetailComponent } from './components/loan-details/tabs/loan-info-wrapper/loan-info-retail/loan-info-retail.component';
import { TransactionsListWidgetModule } from '@backbase/transactions-list-widget-ang';
const components = [
  LoanDetailsWrapperComponent,
  LoanSummaryComponent,
  LoanTransactionsWrapperComponent,
  LoanInfoWrapperComponent,
  PaymentScheduleWrapperComponent,
  PaymentScheduleDetailsWrapperComponent,
  PaymentScheduleDetailsModalHeaderComponent,
  LoanInfoRetailComponent,
];

const modules = [
  LoanInfoPrintButtonModule,
  LoanItemNameModule,
  TransactionsListWidgetModule,
  LoanInfoItemModule,
  LoansHeadingModule,
  LoansCollectionUiAngModule,
  AccountStatementBusinessWidgetModule,
];

@NgModule({
  declarations: [...components],
  imports: [CommonModule, RouterModule, LoansStoreModule, PaymentScheduleStoreModule, EntitlementsModule, ...modules],
  exports: [...components, ...modules],
  providers: [LoansJourneyConfigurationService, LoanMapperService, LoansService, ArrangementsService],
})
export class LoansJourneyModule {
  static forRoot(
    data: { routes: Routes; [key: string]: any } = { routes: loansRoutes },
  ): ModuleWithProviders<LoansJourneyModule> {
    return {
      ngModule: LoansJourneyModule,
      providers: [provideRoutes(data.routes)],
    };
  }
}
