import { NgModule } from '@angular/core';

import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';

import { LoansStoreService } from './store.service';
import { loansReducer, storeKey } from './reducer';
import { LoansEffects } from './effects';

@NgModule({
  imports: [StoreModule.forFeature(storeKey, loansReducer), EffectsModule.forFeature([LoansEffects])],
  providers: [LoansStoreService],
})
export class LoansStoreModule {}
