import { Injectable } from '@angular/core';
import { map, switchMap } from 'rxjs/operators';
import { of } from 'rxjs';

import { Actions, createEffect, ofType } from '@ngrx/effects';

import { catchErrorWithLog } from '../../utils/operators.util';
import { LoansService } from '../../services/loans.service';
import {
  fetchAmortizationPayment,
  fetchAmortizationPaymentError,
  fetchAmortizationPaymentSuccess,
  fetchAmortizationSchedule,
  fetchAmortizationScheduleError,
  fetchAmortizationScheduleSuccess,
} from './actions';

@Injectable()
export class PaymentScheduleEffects {
  constructor(private readonly actions$: Actions, private readonly loansService: LoansService) {}

  fetchAmortizationSchedule$ = createEffect(() =>
    this.actions$.pipe(
      ofType(fetchAmortizationSchedule),
      switchMap(({ params: { loanId, from, append, size } }) =>
        this.loansService.getAmortizationSchedule({ from, size, loanId }).pipe(
          map((response) => fetchAmortizationScheduleSuccess({ response, append })),
          catchErrorWithLog((error) =>
            of(fetchAmortizationScheduleError({ error: error.message || 'Amortization schedule fetch error' })),
          ),
        ),
      ),
    ),
  );

  fetchAmortizationPayment$ = createEffect(() =>
    this.actions$.pipe(
      ofType(fetchAmortizationPayment),
      switchMap(({ params }) =>
        this.loansService.getAmortizationPayment(params).pipe(
          map((amortizedPayment) => fetchAmortizationPaymentSuccess({ amortizedPayment })),
          catchErrorWithLog((error) =>
            of(fetchAmortizationPaymentError({ error: error.message || 'Amortization payment fetch error' })),
          ),
        ),
      ),
    ),
  );
}
